import React, { Component } from 'react';
import './style.css';

class Olimpiad extends Component {
    componentDidMount() {
        var elements = document.querySelectorAll('.olimpiad__name');

        for(var i = 0; i < elements.length; i++) {
            if(elements[i].innerHTML.length >= 79) {
                elements[i].innerHTML = elements[i].innerHTML.match(/[a-z а-яё 1-9 -]+/gi).join(' ').slice(0, 48) + '...';//Обрезает строку, чтобы не было более 51 символов
            }
        }
    }

    render() {
        return (
            <div className='olimpiad'>
            	<div className='wrapper'>
                    <h2 className='olimpiad__title'>Олимпиады</h2>
                    <div className='olimpiad__content'>
                        {this.props.list.map( (item) =>
                            <div className='olimpiad__item' key={item.key}>
                                <span className='olimpiad__img' style={{backgroundImage: 'url(' + item.img + ')'}}></span>
                                <h4 className='olimpiad__name'>{item.title}</h4>
                                <span className='olimpiad__date'>{item.dateMonth}, {item.dateYear}</span>
                                <a href={item.href} target='_blank' className='olimpiad__href'>Посмотреть</a>
                            </div>
                        )}
                    </div>
            	</div>
            </div>
        );
    }
}

export default Olimpiad;


