import React, { Component } from 'react';
import './style.css';

class Up extends Component {
	constructor(props) {
		super(props);

		this.state = {toggle: false};

		this.toggle = this.toggle.bind(this);
	}

	toggle() {
		var height = document.documentElement.clientHeight * 1;

		if(window.pageYOffset >= height && !this.state.toggle) {
			this.setState({toggle: true});
		} else if(window.pageYOffset < height && this.state.toggle) {
			this.setState({toggle: false});
		}
	}

	render() {
		document.body.onscroll = () => {
			this.toggle();
		}

		return(
			<span className='up' onClick={() => {window.scrollTo(0, 0)} } style={{display: this.state.toggle ? 'block' : 'none'}}>
				<span className='up__text'>Go up</span>
			</span>
		)
	}
}

export default Up;


