import React, { Component } from 'react';

class TotalChild2 extends Component {
    componentDidMount() {
        this.props.changeMaxElem();
    }

    render() {
        var total;

        this.props.list.forEach( (item, i, arr) => {
            if(item.href === this.props.match.params.number) total = item;
        });

        return (
            <div className='sameChild'>
            	<div className='wrapper'>
                    <h2 className='sameChild__title'>Итоги / Конкурсы / {this.props.title}</h2>
                    <h3 className='sameChild__title'>{total && total.title}</h3>
                    <div className='sameChild__content'>
                        {total && total.work2.map( (item) =>
                            !this.props.list.add && <div className='sameChild__item' key={item.key}>
                                <span className='sameChild__place'>{item.place} место</span>
                                <span className='sameChild__img' style={{backgroundImage: 'url(' + item.img + ')'}}>
                                     <span className='sameChild__buttonZoom' onClick={this.props.openPopup}></span>
                                </span>
                                <span className='sameChild__name'>{item.fullName}</span>
                                <span className='sameChild__school'>{
                                    item.city ? 'Г. ' + item.city : item.village},
                                    {item.school ? 'Школа ' + item.school : item.kindergarten},
                                    {item.class ? 'Класс ' + item.class : item.age}
                                </span>
                            </div>
                        )}
                        {!total && <h3 className='sameChild__notFound'>Вы попали на страницу, которой не существует !</h3>}
                    </div>
                    <div className='popup' style={{display: this.props.toggle ? 'block' : 'none', backgroundImage: this.props.url}}>
                        <span className='popup__buttonLeft popup__button' onClick={this.props.sliderFlip}></span>
                        <span className='popup__buttonClose' onClick={this.props.changeToggle}></span>
                        <span className='popup__buttonRight popup__button' onClick={this.props.sliderFlip}></span>
                        <span className='popup__count'>
                            {this.props.position} / {this.props.maxElem}
                        </span>
                    </div>
            	</div>
            </div>
        );
    }
}

export default TotalChild2;


