import React, { Component } from 'react';

import './style.css';

class App extends Component {
    render() {
        return (
            <div className='app'>
                <span className='app__img'>
                    <div className='wrapper' style={{backgroundImage: 'url(' + this.props.img + ')'}}></div>
                </span>
                <div className='app__content'>
                    <div className='wrapper'>
                        <h1 className='app__title'>О сайте</h1>
                        <p className='app__text'>
                            Приветствуем вас на сайте <b>"World of English"</b>.
                            Это учебный портал для учеников, их родителей и учителей. Для всех, кому нравится английский язык и не только!
                        </p>
                        <p className='app__text'>
                            <span><b>Тематика сайта</b>: английский язык, прикладное творчество, мастерство во всех его проявлениях и окружающая среда.</span>
                            <span><b>Наша цель</b>: развитие творческих способностей, проверка своих знаний в сфере иностранного языка, обмен опытом различных возрастных групп, знакомство родителей с другой культурой.</span>
                        </p>
                        <p className='app__text'>
                            На сайте Вы можете поучаствовать в олимпиадах, опубликовать свою творческую работу по английскому языку, принять участие в конкурсах, приуроченных к различным событиям и праздникам, а также проголосовать за понравившиеся Вам работы.
                            Желаем удачного путешествия по увлекательному миру английского языка!
                        </p>
                    </div>
                </div>
            </div>
        );
    }
}

export default App;


