import React, { Component } from 'react';

import './style.css';

class Jury extends Component {
    render() {
        return (
            <div className='jury'>
            	<div className='wrapper'>
                    <h2 className='jury__title'>Жюри</h2>
                    <div className='jury__content'>
                        {this.props.list.map( (item) =>
                            <div className='jury__item' key={item.key}>
                                <span className='jury__img' style={{backgroundImage: 'url(' + item.photo + ')'}} ></span>
                                <span className='jury__name'>{item.fullName}</span>
                                <span className='jury__right'>{item.right}</span>
                                <span className='jury__position'>{item.position}</span>
                                <span className='jury__location'>{item.location}</span>
                            </div>
                        )}
                    </div>
            	</div>
            </div>
        );
    }
}

export default Jury;


