import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class CompetitionType extends Component {
    render() {
        return (
            <div className='sameType'>
            	<div className='wrapper'>
                    <h2 className='sameType__title'>Конкурсы / {this.props.list.title}</h2>
                    <div className='sameType__content'>
                        {this.props.list.work && this.props.list.work.map( (item) =>
                            <Link className='sameType__item' to={item.href2 + '/' + item.href} key={item.href}>
                                <span className='sameType__img'></span>
                                <span className='sameType__name'>{item.title}</span>
                            </Link>
                        )}
                        {!this.props.list.work && <h3 className='sameChild__notFound'>Вы попали на страницу, которой не существует !</h3>}
                    </div>
            	</div>
            </div>
        );
    }
}

export default CompetitionType;


