import React, { Component } from 'react';
import './style.css';

class NotFound extends Component {
	constructor(props) {
		super(props);

		this.parallax = this.parallax.bind(this);
	}

	parallax(e) {
		var img = document.querySelector('.notFound__img'),
			arr = [-80, 100, 50, 20, -20];

		for(var i = 0; i < img.children.length; i++) {
			var left = (img.getBoundingClientRect().left - e.clientX) / arr[i] + 'px',
				top  = (img.getBoundingClientRect().top  - e.clientY + 400) / arr[i] + 'px';
			img.children[i].style.transform = 'translate(' + left + ', ' + top + ')';
		}
	}

    render() {
        return (
            <div className='notFound' onMouseMove={this.parallax}>
                <div className='wrapper'>
                	<p className='notFound__title'>404</p>
                	<span className='notFound__img'>
	                	<span className='notFound__seaHorse'></span>
	                	<span className='notFound__nemo'></span>
	                	<span className='notFound__friend'></span>
	                	<span className='notFound__sharp'></span>
	                	<span className='notFound__star'></span>
	                </span>
                </div>
            </div>
        );
    }
}

export default NotFound;


