import React, { Component } from 'react';

import './style.css';

class CompetitionChild extends Component {
    render() {
        var competition;

        this.props.list.work.forEach( (item, i, arr) => {
            if(item.href === this.props.match.params.number) competition = item;
        });

        return (
            <div className='competitionChild'>
            	<div className='wrapper'>
                    <h2 className='competitionChild__title'>Конкурсы / {this.props.title}</h2>
                    <h3 className='competitionChild__title2'>{competition.work && competition.title}</h3>
                    <div className='competitionChild__content'>
                        {competition.work && competition.work.map( (item) =>
                            <div className='competitionChild__item' key={item.key}>
                                <span className='competitionChild__img' style={{backgroundImage: 'url(' + item.img + ')'}}>
                                    {item.text && item.text.map( (item) =>
                                        <p>{item}</p>
                                    )}
                                </span>
                                <span className='competitionChild__name'>{item.fullName}</span>
                                <span className='competitionChild__school'>Г.{item.city}, Школа {item.school}, Класс {item.class}</span>
                            </div>
                        )}
                        {!competition.work && <h3 className='sameChild__notFound'>Вы попали на страницу, которой не существует !</h3>}
                    </div>
            	</div>
            </div>
        );
    }
}

export default CompetitionChild;


