import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import './style.css';

class Same extends Component {
    render() {
        return (
            <div className='same'>
                <span className='same__background' style={{backgroundImage: 'url(' + this.props.img + ')' }}></span>
                <div className='wrapper'>
                	<h2 className="same__title">{this.props.title}</h2>
                    {this.props.list && this.props.list.map( (items) =>
                    	<div className={this.props.href === '/Exhibitions/' ? 'same__itemLighter' : 'same__item'} key={items.key}>
                            <Link to={this.props.href + items.href} className='same__type'>{items.title}</Link>
                            <ul className='same__list0'>
                                {items.work && items.work.map( (item) =>
                                    <li className={item.fullName ? 'same__list0__none' : ''} key={item.key}>
                                        {!item.add && <Link to={this.props.href + items.href + '/' + item.href} className='same__href0'>{item.title}</Link>}

                                        {item.add && <span className='same__href1'>{item.title}</span>}
                                        {item.work2 && item.sibling &&
                                            <ul className='same__list1'>
                                                {item.work2 && item.work2.map( (item2) =>
                                                    <li key={item2.href}>
                                                        <Link to={'/Total/Competition/' + item.href + '/' + item2.href}>{item2.title}</Link>
                                                    </li>
                                                )}
                                            </ul>
                                        }
                                    </li>
                                )}
                            </ul>
                    	</div>
                    )}
                </div>
            </div>
        );
    }
}

export default Same;


