import React from 'react';
import { HashRouter, Route, Switch } from 'react-router-dom';

import img1 from './city.jpg';
import img2 from './city2.jpg';
import img3 from './city3.jpg';
import img4 from './lond.jpg';

/*
	---------------------
	|					|
	|		Pages		|
	|					|
	---------------------
*/
import Header from './components/Header';
import Footer from './components/Footer';
import Up from './components/Up';

import App from './components/App';

import Exhibition from './components/Exhibition';

import Same from './components/Same';
import CompetitionType from './components/CompetitionType';
import CompetitionChild from './components/CompetitionChild';

import Olimpiad from './components/Olimpiad';

import TotalType from './components/TotalType';
import TotalChild from './components/TotalChild';
import TotalChild2 from './components/TotalChild2';

import Jury from './components/Jury';

import NotFound from './components/NotFound';

/*
	---------------------
	|					|
	|		Works		|
	|					|
	---------------------
*/
import ExhibitionList from './components/ExhibitionWork';
import CompetitionList from './components/CompetitionWork';
import OlimpiadList from './components/OlimpiadWork';
import TotalList from './components/TotalWork';
import JuryList from './components/Jury/jury.js';

var menu = [
    {
        title: 'Выставки',
        href: '/Exhibitions',
        key: 1,
		nav2: [
			{
				title: 'Mother`s day',
				href: '/Exhibitions/1',
				key: 1
			}
		]
    },
    {
        title: 'Конкурсы',
        href: '/Competitions',
        key: 2,
        nav2: [
            {
            	title: 'Начальная школа',
            	href: '/Competitions/Primary',
            	key: 1
            },
            {
            	title: 'Среднее звено',
            	href: '/Competitions/Middle',
            	key: 2
            },
            {
            	title: 'Старшее звено',
            	href: '/Competitions/High',
            	key: 3
            }
        ]
    },
    {
        title: 'Олимпиады',
        href: '/Olimpiad',
        key: 3
    },
    {
        title: 'Итоги',
        href: '/Total',
        key: 4,
        nav2: [
            {
            	title: 'Выставки',
            	href: '/Total/Exhibition',
            	key: 1
            },
            {
            	title: 'Конкурсы',
            	href: '/Total/Competition',
            	key: 2
            },
            {
            	title: 'Олимпиады',
            	href: '/Total/Olimpiad',
            	key: 3
            }
        ]
    },
    {
        title: 'Жюри',
        href: '/Jury',
        key: 5
    }
];

class Routes extends React.Component {
	constructor(props) {
        super(props);

        this.state = {
            url: '',
            toggle: false,
            target: '',
            position: '',
            maxElem: '',
        };

        this.changeMaxElem = this.changeMaxElem.bind(this);
        this.openPopup     = this.openPopup.bind(this);
        this.changeToggle  = this.changeToggle.bind(this);
        this.sliderFlip    = this.sliderFlip.bind(this);
    }

    changeMaxElem() {
        this.setState({maxElem: document.querySelectorAll('.sameChild__item').length});
    }

    openPopup(e) {
        var target = e.target,
            url    = getComputedStyle(target.parentNode).backgroundImage,
            num;

        for(var i = 0; i < this.state.maxElem; i++) {
            if(document.querySelectorAll('.sameChild__item')[i] === target.parentNode.parentNode) num = i + 1;
        }

        this.setState({
            toggle: !this.state.toggle,
            url: url,
            target: target.parentNode.parentNode,
            position: num
        });
    }

    changeToggle() {
        this.setState({toggle: !this.state.toggle});
    }

    sliderFlip(e) {
    	var target;
        switch(e.target.classList[0]) {
            case 'popup__buttonLeft':
                target = this.state.target.previousSibling || false;
                this.setState({position: !target ? this.state.position : this.state.position - 1});
                break;
            case 'popup__buttonRight':
                target = this.state.target.nextSibling || false;
                this.setState({position: !target ? this.state.position : this.state.position + 1});
                break;
            default:
            	break;
        }

        if(!target) return;

        this.setState({
            url: getComputedStyle(target.children[1]).backgroundImage,
            target: target
        });
    }

	render() {
		document.documentElement.style.overflowY = this.state.toggle ? 'hidden' : '';

		return(
			<HashRouter>
				<div>
					<Header menu={menu}/>
					<Up />
					<main>
						<Switch>
							<Route path='/' exact key='1' render={ (props) => (
								<App img={img4}/>
							)} />

							<Route path='/Exhibitions' exact key='2' render={ (props) => (
								<Same
									{...props}
									list={ExhibitionList}
									img={img1}
									title='Выставки'
									href='/Exhibitions/'
								/>
							)} />
							<Route
								path={'/Exhibitions/:number'}
								key='3'
								render={ (props) => (
									<Exhibition
										{...props}
										list={ExhibitionList}
										openPopup={this.openPopup}
										toggle={this.state.toggle}
										url={this.state.url}
										sliderFlip={this.sliderFlip}
										changeToggle={this.changeToggle}
										position={this.state.position}
										maxElem={this.state.maxElem}
										changeMaxElem={this.changeMaxElem}
									/>
								)}
							/>

							<Route path='/Competitions'
								exact
								key='4'
								render={ (props) => (
									<Same
										{...props}
										list={CompetitionList}
										img={img2}
										title="Конкурсы"
										href='/Competitions/'
									/>
								)
							}/>
							{CompetitionList.map( (item) =>
								<Route
									path={'/Competitions/' + item.href}
									exact
									key={item.key}
									render={ (props) => (
										<CompetitionType {...props} list={item} />
									)} />
							)}
							{CompetitionList.map( (item) =>
								<Route
									path={'/Competitions/' + item.href + '/:number'}
									key={item.key}
									render={ (props) => (
										<CompetitionChild {...props} title={item.title} list={item} />
									)
								}/>
							)}

							<Route
								path='/Olimpiad'
								key='5'
								render={ (props) => (
									<Olimpiad {...props} list={OlimpiadList} />
								)
							}/>

							<Route path='/Total'
								exact
								key='6'
								render={ (props) => (
								<Same {...props}
									list={TotalList}
									img={img3}
									title="Итоги"
									href='/Total/'
								/>
							)} />
							{TotalList.map( (item) =>
								<Route
									path={'/Total/' + item.href}
									key={item.key}
									exact
									render={ (props) => (
									<TotalType
										{...props}
										list={item.work}
										href={item.href} 
										title={item.title}
									/>
								)} />
							)}

							<Route
								path={'/Total/Exhibition/:number'}
								key='7'
								render={ (props) => (
									<TotalChild
										{...props}
										title='Выставки'
										list={TotalList[0]}
										openPopup={this.openPopup}
										toggle={this.state.toggle}
										url={this.state.url}
										sliderFlip={this.sliderFlip}
										changeToggle={this.changeToggle}
										position={this.state.position}
										maxElem={this.state.maxElem}
										changeMaxElem={this.changeMaxElem}
									/>
								)
							}/>
							{TotalList[1].work.map( (item) =>
								<Route
									path={'/Total/Competition/' + item.href + '/:number'}
									exact
									key='8'
									render={ (props) => (
										<TotalChild2 {...props}
											title={item.title}
											list={item.work2}
											openPopup={this.openPopup}
											toggle={this.state.toggle}
											url={this.state.url}
											sliderFlip={this.sliderFlip}
											changeToggle={this.changeToggle}
											position={this.state.position}
											maxElem={this.state.maxElem}
											changeMaxElem={this.changeMaxElem}
										/>
									)}
								/>
							)}
							<Route
								path={'/Total/Olimpiad/:number'}
								key='9'
								render={ (props) => (
									<TotalChild
										{...props}
										title='Олимпиады'
										list={TotalList[2]}
										openPopup={this.openPopup}
										toggle={this.state.toggle}
										url={this.state.url}
										sliderFlip={this.sliderFlip}
										changeToggle={this.changeToggle}
										position={this.state.position}
										maxElem={this.state.maxElem}
										changeMaxElem={this.changeMaxElem}
									/>
								)}
							/>


							<Route path='/Jury'
								exact
								key='10'
								render={ (props) => (
									<Jury {...props} list={JuryList} />
								)
							}/>

							<Route path='*' key='11' component={NotFound} />
						</Switch>
					</main>
					<Footer menu={menu}/>
				</div>
			</HashRouter>
		)
	}
};

export default Routes;


