import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './style.css';

class Header extends Component {
	constructor(props) {
		super(props);

		this.state = {open: false};
		this.handleClick = this.handleClick.bind(this);
		this.valide      = this.valide.bind(this);
	}

	handleClick() {
		if(this.valide() ) this.setState({open:  !this.state.open});
	}

	valide() {
		document.body.style.overflow = 'hidden';

		if(document.body.clientWidth <= 480) {
			document.body.style.overflow = '';
			return true;
		} else {
			document.body.style.overflow = '';
			return false;
		}
	}

	render() {
		window.onresize = () => {
			this.setState({open: this.valide() ? false : true});
		};

		document.addEventListener('DOMContentLoaded', () => {
			this.setState({open: this.valide() ? false : true});
		});
		
		return (
			<header className="header">
				<div className='wrapper'>
					<Link to='/' className="header__logo">
						World of English
					</Link>
					<span className='header__mobnav' onClick={this.handleClick}></span>
					<ul className="header__nav" onClick={this.handleClick} style={{display: this.state.open ? 'inline-block' : 'none'}}>
						{this.props.menu.map( (item) =>
							<li key={item.key}>
								<Link to={item.href}>
									{item.title}
									{item.nav2 && <span></span>}
								</Link>
								{item.nav2 && <ul className='header__nav2'>
									{item.nav2.map( (item) =>
										<li key={item.key}>
											<Link to={item.href}>{item.title}</Link>
										</li>
									)}
								</ul>}
							</li>
						)}
					</ul>
				</div>
			</header>
		);
	}
}

export default Header;


