import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class TotalType extends Component {
    render() {
        return (
            <div className='sameType'>
            	<div className='wrapper'>
                    <h2 className='sameType__title'>Итоги / {this.props.title}</h2>
                    <div className='sameType__content'>
                        {this.props.list && this.props.list.map( (item) =>
                            !item.add && <Link className='sameType__item' to={this.props.href + '/' + item.href} key={item.href}>
                                <span className='sameType__img' style={{backgroundImage: 'url(' + item.img + ')'}}></span>
                                <span className='sameType__name'>{item.title}</span>
                            </Link>
                        )}

                        {this.props.list && this.props.list.map( (items) =>
                            items.add &&
                            <div key={items.href}>
                                <h3 className='sameType__title2'>{items.title}</h3>
                                {items.work2 && items.work2.map( (item) =>
                                    <Link className='sameType__item' to={this.props.href + '/' + items.href + '/' + item.href} key={item.href}>
                                        <span className='sameType__img' style={{backgroundImage: 'url(' + item.img + ')'}}></span>
                                        <span className='sameType__name'>{item.title}</span>
                                    </Link>
                                )}
                            </div>
                        )}
                        {(!this.props.list || this.props.list.length === 0) && <h3 className='sameChild__notFound'>Вы попали на страницу, которой не существует !</h3>}
                    </div>
            	</div>
            </div>
        );
    }
}

export default TotalType;


