import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './style.css';

class Footer extends Component {
    render() {
        return (
            <footer className="footer">
                <div className='footer__main'>
                    <div className='wrapper'>
                        {this.props.menu.map( (item) => 
                            <span className='sitemap' key={item.key}>
                                <Link className='sitemap__title' to={item.href}>{item.title}</Link>
                                <ul className='sitemap__text'>
                                    {item.nav2 && item.nav2.map( (item) => 
                                        <li key={item.key}>
                                            <Link to={item.href}>{item.title}</Link>
                                        </li>
                                    )}
                                </ul>
                            </span>
                        )}
                    </div>
                </div>
                <div className='allright'>
                    <div className='wrapper'>
                        <span className='allright__right'><b>© </b>2017 Школа 6 Кириши</span>
                        <span className='allright__coder'>Разработка сайта <a href="https://evgenyproshin.ru" target='_blank'>Евгений Прошин</a></span>
                    </div>
                </div>
            </footer>
        );
    }
}

export default Footer;


